html {
  font-size: 62.5%;
  box-sizing: border-box; }

*, *:before, *:after {
  box-sizing: inherit; }

body {
  font-family: "Poppins", sans-serif;
  font-size: 1.6rem; }

p {
  font-size: 2rem;
  color: #161618; }

.content {
  max-width: 120rem;
  width: 95%;
  margin: 0 auto; }

a {
  text-decoration: none; }

img {
  max-width: 100%; }

h1, h2, h3 {
  margin: 0 0 2rem 0;
  font-weight: 900;
  color: #2274A5;
  text-align: center; }

h1 {
  font-size: 4rem; }

h2 {
  font-size: 3.6rem; }

h3 {
  font-size: 3rem; }

body {
  background-color: #161618; }

@media (min-width: 768px) {
  .barber-content {
    height: 100vh;
    display: grid;
    grid-template-columns: 1fr 1fr; } }

.barber-content .image {
  height: 40rem;
  background-image: url("/build/img/1.jpg");
  background-size: cover;
  background-position: center center; }
  @media (min-width: 768px) {
    .barber-content .image {
      height: auto; } }

.app {
  padding: 4rem;
  box-shadow: -16px -16px 27px -16px rgba(34, 116, 165, 0.6); }
  .app .section {
    display: none;
    margin-top: 1rem; }
    .app .section .align {
      text-align: center; }
    .app .section h2 {
      color: #724120; }
    .app .section p {
      color: #fff; }

.show-section {
  display: block !important; }

@media (min-width: 480px) {
  .tabs {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem; } }

.tabs button {
  width: 100%;
  margin: 1rem 0;
  background-color: #fff;
  font-size: 1.4rem;
  font-weight: bold;
  padding: 1rem;
  text-align: center;
  border: none;
  border-radius: 2rem;
  outline: none;
  text-transform: uppercase;
  transition: .2s ease-in-out; }
  .tabs button.current {
    background-color: #2274A5;
    color: #fff; }
  .tabs button:hover {
    cursor: pointer;
    background-color: #2274A5; }

@media (min-width: 480px) {
  .services-list {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 2rem; } }

.services-list .service {
  background-color: #fff;
  border-radius: 2rem;
  padding: 2rem 0;
  margin-bottom: 2rem;
  transition: .2s ease-in-out;
  text-align: center;
  cursor: pointer; }
  @media (min-width: 480px) {
    .services-list .service {
      margin: 0; } }
  .services-list .service p {
    margin: 0 0 2rem 0; }
  .services-list .service .service-name {
    color: #161618;
    font-weight: bold; }
  .services-list .service .service-price {
    color: #2274A5;
    font-weight: 900;
    margin: 0;
    font-size: 3rem; }
  .services-list .service:hover {
    background-color: #2274A5; }
    .services-list .service:hover p {
      color: #fff; }
  .services-list .service.selected {
    background-color: #2274A5; }
    .services-list .service.selected .service-price {
      color: #fff; }
      .services-list .service.selected .service-price::after {
        content: " ✔"; }

@media (min-width: 480px) {
  .pagination {
    display: flex;
    justify-content: space-around;
    margin-top: 3rem; } }

.pagination button {
  background-color: #2274A5;
  padding: 1rem 3rem;
  text-align: center;
  border: none;
  outline: none;
  color: #fff;
  font-weight: bold;
  text-transform: uppercase;
  font-size: 2rem;
  width: 100%;
  border-radius: 2rem;
  transition: 0.2s ease-in-out;
  margin-bottom: 2rem; }
  @media (min-width: 480px) {
    .pagination button {
      width: 20rem;
      margin: 0; } }
  .pagination button.hidde {
    display: none; }
  .pagination button:hover {
    cursor: pointer;
    color: #161618; }

.field {
  display: flex;
  align-items: center;
  margin-bottom: 2rem; }
  .field label {
    flex: 0 0 10rem;
    color: #fff; }
  .field input {
    flex: 1;
    padding: 1rem;
    border-radius: 2rem;
    border: none;
    outline: none; }
    .field input:focus {
      box-shadow: 1px 15px 7px -4px #2274a5; }

.alert {
  padding: 2rem;
  text-align: center;
  color: #161618;
  font-weight: bold;
  width: 100%;
  display: block;
  border-radius: 2rem; }
  .alert.error {
    background-color: #C20000; }
  .alert.warning {
    background-color: #FFAB00; }

.error-date {
  margin: 2rem 0;
  text-transform: uppercase; }

.summary-content {
  margin-bottom: 2rem 0; }
  .summary-content p {
    margin-bottom: .5rem;
    text-align: left; }
    .summary-content p span {
      font-weight: bold;
      color: #2274A5;
      font-size: 2.4rem; }
  .summary-content .service-content h3 {
    margin: 5rem;
    color: #724120; }
  .summary-content .service-content .service-div {
    margin-bottom: 2rem;
    padding-bottom: 2rem;
    border-bottom: 1px solid gray; }
    .summary-content .service-content .service-div p {
      text-align: center; }
    .summary-content .service-content .service-div .price {
      font-size: 2.4rem;
      font-weight: bold;
      color: #2274A5; }

.totals {
  text-align: end !important; }
